@import "./styles.scss";

.account{
    margin: 2em;
    min-height: 30em;

.heading{
margin: auto;
text-align: center;
}

.form{
display: flex;
flex-basis: 40%;
}
form{

    font-size: large;
    width: 50%;
    margin: 4em;
  fieldset{
      padding-top: 40px;
      border: medium solid $primary-color;
      border-radius: 15px;

legend{
        border-radius: 5px;
        
    }
    input{
        height: 40px;
        width: 400px;
        margin-bottom: 20px;
        margin-left: 30px;
        border: 2px solid $layout-color;
        border-radius: 7px;
    }
    label{
        margin-left: 30px;
    }

    button{
        height: 50px;
        width: 400px;
        margin-bottom: 20px;
        margin-left: 30px;
        color: white;
        background-color: $primary-color;
        border: 2px solid $layout-color;
        border-radius: 7px;
    }
    }

    

}

}